@import url(https://fonts.googleapis.com/css?family=Ubuntu);


.main-container{
    display: flex;
    color: white;
    text-align: left;
    font-family: 'Ubuntu', sans-serif;

}
.about{
    flex: 1;
}
.skills{
    flex: 1; 
}
.skill-card {
    width: 100px;           /* σταθερό μέγεθος “κουτιού” */
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill-card img {
  width: 100%;
  height: 100%;
  object-fit: contain;     /* ✅ διατηρεί τις αναλογίες χωρίς παραμόρφωση */
}
.skills-grid {
  display: flex;
  flex-direction: row; /* 👉 Οριζόντια διάταξη */
  justify-content:space-evenly; /* Κεντράρει οριζόντια */
  align-items: center; /* Κεντράρει κάθετα */
  gap: 10px; /* Απόσταση ανάμεσα στις εικόνες */
  flex-wrap: nowrap; /* Δεν επιτρέπει να πάνε σε δεύτερη γραμμή */
    
}
